.current-location{
    border-bottom: 2px dotted #ef9f40;
    margin-left: 5px;
    color: $green;
}
.office-location{
    border-bottom: 2px solid #c9c8c6;
    .container{
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: center;
    }
}
.office-location__select-block{
    display: flex;
    align-items: baseline;
    cursor: pointer;
    p{
        font-weight: 600;
        font-size: 1em; 
        margin-right: 5px;
    }
}
.office-location-select{
    position: relative;
  
    input[name="current-option"]{
        border: none;
        outline: none;
    }
}
.office-location-select-option{
    position: relative;
    input{
        opacity: 0;
        position: absolute;
    }
}
.office-location-select-current-option{
    input{
        border: none;
        color: #ef9f40;
        text-decoration: dotted underline;
        line-height: 1.5em;
    }
}
.office-location-select-options{
    flex-direction: column;
    display: none;
    position: absolute;
    z-index: 300;
    background-color: #f7f7f7;
    align-items: center;
    padding-left: 30px;
    padding-right: 30px;
    padding-bottom: 7px;
    padding-top: 7px;

    &::before{
        content: "";
        display: block;
        position: absolute;
        top: -20px;
        left: 45px;
        border: 10px solid transparent;	
        border-bottom: 10px solid #f7f7f7;
    }
}
.office-location-select-option{
    padding-top: 10px;
    padding-bottom: 3px;
    color: #2a2a28;
    padding-left: 10px;
    padding-right: 10px;
    transition: opacity 0.3s ease-in;
    border-bottom: 2px dotted #ef9f40;
    margin-bottom: 12px;
    &:hover{
        opacity: 0.6;
    }
}
.office-location-select-options.open{
    display: flex;
}

@media(max-width: 767px){
    .office-location__select-block{
        font-size: 0.875em;
    }
}