body {
	font-family: "Open Sans";
	font-size: 16px;
	width: 100%;
	height: 100%;
	font-weight: normal;
	box-sizing: border-box;
}
h1, h2, h3, h4, h5, h6, ul {
	padding: 0;
	margin: 0;
}
html{
	font-size: 16px;
	height: 100%;
	box-sizing: border-box;
}
.container {
	@include container();
	@include sm(max-width, 510px);
	@include md(max-width, 690px);
	@include lg(max-width, 930px);
	@include xl(max-width, 1110px);
}
.visually-hidden {
	position: absolute !important;
	clip: rect(1px 1px 1px 1px); /* IE6, IE7 */
	clip: rect(1px, 1px, 1px, 1px);
	padding:0 !important;
	border:0 !important;
	height: 1px !important; 
	width: 1px !important; 
	overflow: hidden;
}

.img-responsive {
	width: 100%;
	height: auto;
}

.icon {
	display: inline-block;
	width: 1.1em;
	height: 1.1em;
	stroke-width: 0;
	stroke: currentColor;
	fill: currentColor;
}