.primary-select{
    width: 100%;
    height: 40px;
    background-color: transparent;
    background-image: url(../img/dropdown-arrow.svg);
    background-size: 15px 15px;
    background-repeat: no-repeat;
    padding-left: 10px;
    padding-right: 10px;
    background-position-y: center;
    background-position-x: calc(100% - 5px);
    border-radius: 6px;
}
.primary-input{
    width: 100%;
    height: 40px;
    background-color: transparent;
    background-size: 15px 15px;
    background-repeat: no-repeat;
    padding-left: 10px;
    padding-right: 10px;
    box-sizing: border-box;
    border-radius: 6px;
}
.primary-textarea{
    width: 100%;
    border: 2px solid #d9dbda;
    outline: $green;
    min-height: 80px;
    resize: none;
    padding: 0;
    border-radius: 6px;
}

@media(max-width: 991px){
    .filter-select{
        font-size: 14px;
    }
    .filter__search{
        input{
            &::placeholder{
                font-size: 14px;
            }
        }
    }
}
