.actions__wrapper{
    display: flex;
    flex-wrap: wrap;
    margin-left: -15px;
    margin-right: -15px;
    box-sizing: border-box;
    .actions__item{
        max-width: 540px;
        margin-left: 15px;
        margin-right: 15px;
        box-sizing: border-box;
        margin-bottom: 15px;
        .time-to-end-action__text{
            display: flex;
            font-size: 0.875em;
            white-space: nowrap;
        }
    }
}