.tabs-list{
    list-style: none;
    display: flex;
    margin-bottom: 20px;
    border-bottom: 5px solid #68ab4c;


    &__item{
        display: block;
        width: 100%;  
    a{
        padding-left: 20px;
        padding-right: 20px;
        display: block;
        color: inherit;
        min-height: 40px;
        text-decoration: none;
        padding-top: 10px;
        padding-bottom: 10px;
        text-align: center;
        box-sizing: border-box;
        transition: all 0.1s ease-in;
        &:hover{
            background-color: $green;
            color: #fff;
        }
    }
    }
    &__item.active{
        a{
            background-color: $green;
            color: #fff;
        }
    }
}