.services-list__container{
    background: #fff;
}
.services-list{
    margin-top: 30px;
}
.services-list__tabs{
    display: flex;
    align-items: baseline;
    flex-wrap: wrap;
    list-style: none;
    border-bottom: 5px solid $green;
    cursor: pointer;
    font-weight: 600;
}
.services-list__tab{
    border-left: 1px solid #dcdedd;
    text-align: center;
    display: flex;
    justify-content: center;
    width: calc(100% / 4 - 1px);
    padding-top: 25px;
    padding-bottom: 25px;
}
.services-list__tab.active{
    background-color: $green;
    color: #fff;
    border: none;
}
.services-list__list{
    list-style: none;
    display: none;
    padding-left: 15px;
    padding-right: 15px;
}
.services-list__list.active{
    display: flex;
    width: 70%;
    flex-wrap: wrap;
}
.services-list__tabs-content{
    display: flex;
    padding-top: 57px;
    padding-bottom: 50px;
}
.services-list__list-item{
    width: 50%;
    margin-bottom: 10px;
    a{
        color: #000;
        font-size: 1em;
        font-weight: 400;
        text-decoration: none;
        transition: all .3s ease-in;
        &:hover{
            color: $green;
        }
    }
}
.services-list__aditional{
    width: 30%;
    p{
        color: #66ac4a;
        padding-left: 10px;
        padding-bottom: 10px;
        border-bottom: 1px solid #dcdedd;
    }
}
.services-list__aditional-list{
    list-style: none;
   
    .services-list__list-item{
        width: 100%;
    }
}

@media(max-width: 767px){
    .services-list__list-item{
        font-size: 0.875em;
    }
    .services-list__tab{
        width: calc(100% / 2 - 1px)
    }
    .services-list__tabs-content{
        flex-direction: column;
        align-items: center;
        padding-top: 25px;
        padding-bottom: 25px;
    }
    .services-list__list.active{
        width: 100%;
    }
    .services-list__aditional{
        width: 100%;
    }
    .services-list__tab{
        border-left: 1px solid #dcdedd;
    }
}