.schedule-table__row{
    display: flex;
    align-items: stretch;
    margin-bottom: 10px;
    flex-wrap: wrap;
    margin-right: -2.5px;
    margin-left: -2.5px;
}
.schedule-table__col{
    width: 100%;
    box-sizing: border-box;
    max-width: 180px;
    margin-right: 2.5px;
    margin-left: 2.5px;
    border: 2px solid #d9dbda;

    
}
.schedule-table__col-head{
    background: #d9dbda;
    text-align: center;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 600;
}
.schedule__wrapper{
    margin-top: 25px;
}
.schedule-table__col-body{
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 80px;
    padding-top: 15px;
    box-sizing: border-box;
    margin-top: 5px;
    transition: all .3s ease-in;
    &:hover{
        border-color: $green;
    }
    span{
        cursor: pointer;
        transition: all .3s ease-in;
        &:hover{
            color: $green;
        }
    }
}

@media(max-width: 460px){
    .schedule-table__col{
        max-width: 100%;
        width: calc(100% / 2 - 5px);
        .doctors__list-item-name{
            margin-top: 5px;
        }
        .doctors__list-item-position{
            margin-bottom: 5px;
            height: 100%;
        }
    }
}
.schedule__table-head{
    background: #eee;
    height: 40px;
    padding-left: 5px;
    padding-right: 5px;
    margin-left: 0;
    margin-right: 0;
    align-items: center;    
    .schedule-table__col{
        border: none;
        font-weight: 600;
        text-align: center;
        margin: 0;
    }
}
.schedule-table--full{
    .schedule-table__col{
        max-width: 154px;
    }
    .schedule__table-head{
        .schedule-table__col{
            max-width: 154px;
        }
    }
    .doctors__list-item-position{
        font-size: 14px;
        color: $green;
        text-align: center;
    }
    .doctors__list-item-name{
        text-align: center;
       a{
        color: $green;
        font-size: 14px;
        text-decoration: none;
        
       } 
    }  
}
