@import '../../../node_modules/slick-slider/slick/slick.scss';
@import '../../../node_modules/slick-slider/slick/slick-theme.scss';

.slick-dots{
    li{
        margin: 0;
        button{
            width: 10px;
            height: 10px;
            background-color: #838383;
            border-radius: 50%;
            margin: 0;
            padding: 0;
            border: none;
        }
    }
    .slick-active{
        button{
            background-color: $green;
        }
    }
}