
.pulsating-circle {
    position: fixed;
    right: 0;
    top: 80%;
    transform: translateX(-50%) translateY(-50%);
    width: 100px;
    height: 100px;
    cursor: pointer;
    &:hover{
       &::after{
           background: $green;
       }
    }
    
    &:before {
      content: '';
      position: relative;
      display: block;
      width: 250%;
      height: 250%;
      box-sizing: border-box;
      margin-left: -75%;
      margin-top: -75%;
      border-radius: 50%;
      background-color: rgba(104, 171, 76, 0.9);
      animation: pulse-ring 1.25s cubic-bezier(0.215, 0.61, 0.355, 1) infinite;
    }
    
    &:after {
      content: '';
      position: absolute;
      left: 0; 
      top: 0;
      display: block;
      width: 100%;
      height: 100%;
      background-color: rgba(104, 171, 76, 0.9);
      border-radius: 50%;
      box-shadow: 0 0 8px rgba(0,0,0,.3);
      animation: pulse-dot 1.25s cubic-bezier(0.455, 0.03, 0.515, 0.955) -.4s infinite;
    }
  }
  
  @keyframes pulse-ring {
    0% {
      transform: scale(.33);
    }
    80%, 100% {
      opacity: 0;
    }
  }
  
  @keyframes pulse-dot {
    0% {
      transform: scale(.8);
    }
    50% {
      transform: scale(1);
    }
    100% {
      transform: scale(.8);
    }
  }
  .pulsating-circle__text{
      font-size: 0.825em;
      color: #fff;
      text-transform: uppercase;
      display: block;
      position: absolute;
      top: 50%;
      z-index: 200;
      margin-top: -13px;
      text-align: center;
      font-weight: 600;
      line-height: 1.3em;
  }

  @media(max-width: 767px){
    .pulsating-circle{
      display: none;
    }
  }

  .pulsating-circle--ask-question{
    left: 100px;
    &::after{
        background: #f6a43e;
    }
    &::before{
        background: #f6a43e;
    }
  }