.doctor-description{
    margin-top: 53px;
    box-sizing: border-box;
   
}
.doctor-description__main-information{
    padding-left: 25px;
    padding-right: 25px;
    display: flex;
    background: #fff;
    padding-top: 35px;
    padding-bottom: 30px;
    
}
.doctor-description__main-information-text{
    padding-left: 40px;
}
.doctor-description__doctor-name{
    color: #5b5c5e;
    font-size: 1.750em;
    font-weight: 600;
    margin-bottom: 25px;
}
.doctor-description__achievements,
.doctor-description__experience,
.doctor-description__specialization{
    letter-spacing: .7px;
    line-height: 1.3em;
}
.doctor-description__experience{
    margin-bottom: 0;
}
.doctor-description__achievements{
    margin: 0;
    margin-bottom: 50px;
}

.doctor-description__tab-content-list{
    list-style: none;
}
.doctor-description__tab-content-list-item.no-flex{
    display: block !important;
}
.doctor-description__tab-content-list-item{
    margin-bottom: 40px;
    display: flex;
    align-items: center;
}
.doctor-description__tab-content-list-item-name{
    font-size: 1.500em;
    font-weight: normal;
    display: inline-block;
    vertical-align: middle;
    padding-left: 25px;
}
.doctor-description__tab-content-list-item-icon{
    display: inline-block;
    width: 90px;
    height: 90px;
    min-width: 90px;
    min-height: 90px;
    border-radius: 50%;
    background: #e6e7e9;
    position: relative;
    vertical-align: middle;
    svg{
        color: $green;
        width: 40px;
        height: 40px;
        position: absolute;
        top: 50%;
        left: 50%;
        margin-left: -20px;
        margin-top: -20px;
    }
}
.doctor-description__tab-content-list-item-text{
    margin-top: 15px;
   span{
       font-size: 14px;
       font-weight: 400;
   }
}
.doctor-description__tabs{
    background: #fff;
    margin-bottom: 45px !important;
}

@media(max-width: 767px){
    .doctor-description__tabs{
        flex-wrap: wrap;
    }
    .doctor-description__tab{
        width: auto;
    }
    .doctor-description__main-information{
        flex-direction: column;
        align-items: center;
        text-align: center;
        .btn{
            margin: 0 auto;
        }
    }
}