.footer{
    background-color: #f7f7f7;
    padding-bottom: 15px;
    padding-top: 10px;
    &__top{
        border-bottom: 2px solid #c9c8c6;
        font-weight: 600;
        flex-wrap: wrap;
        padding-top: 10px;
        padding-bottom: 10px;
        & .container{
            display: flex;
            align-items: baseline;
            justify-content: space-between;
        }
    }
    .footer__text{
        margin: 0;
        margin-top: 10px;
        margin-bottom: 10px;
        font-size: 1.5em;
    }
    &__company-number{
        color: $green;
        margin-right: 10px;
        margin-bottom: 10px;
        font-size: 1.5em;
        text-decoration: none;
       
    }
    &__bot{
        display: flex;
        justify-content: center;
        padding-top: 15px;  
    }
}
@media(max-width: 991px){
    .footer{
        .footer__company-number,
        .footer__text{
            font-size: 1em;
        }

    }
}
@media(max-width: 767px){
    .footer{
        &__top{
            .container{
                flex-direction: column;
                align-items: center;
            }
        }
        .footer__text{
            margin-right: 0;
            margin-left: 0;
            white-space: nowrap;
            margin-bottom: 0;
        }
        .footer__company-number{
            margin-left: 0;
            margin-right: 0;
            white-space: nowrap;
            font-size: 1em;
            margin-bottom: 0;
            margin-top: 10px;
        }
    }
}