.patient-information{
   padding-bottom: 90px;
    .doctors__filter-item{
        margin-left: 0;
    }
}
.patient-information__wrapper{
    padding-top: 30px;
    background: #fff;
    margin-left: -15px;
    margin-right: -15px;
    padding-left: 15px;
    padding-right: 15px;
    padding-bottom: 30px;
}
.patient-information__list{
    list-style: none;
}
.patient-information__item.open{
    &::after{
        transform: rotate(180deg);
    }
    .patient-information__item-body{
        display: block;
    }
}

.patient-information__item{
    color: #2a2a28;
    margin-top: 20px;
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 8px;
    padding-bottom: 11px;
    position: relative;
    border-radius: 8px;
    cursor: pointer;
    .patient-information__item-head{
        width: auto;
        border-bottom: 2px dotted $green;
        display: flex;
        align-items: center;
        img{
            width: 30px;
            height: 51px;
            margin-right: 20px;
        }
    }
    &::after{
        content: "";
        display: block;
        position: absolute;
        transition: all 0.3s ease-in;
        right: 20px;
        top: 30px;
        margin-top: -5px;
        border: 10px solid transparent;	border-top: 10px solid $green;
    }
    &:nth-child(2n){
        .patient-information__item-head{
            border-bottom: 2px dotted #ef9f40;
        }
       
        &::after{
            border: 10px solid transparent;	border-top: 10px solid #ef9f40;
        }
    }
}
.patient-information__item-head{
    font-weight: 600;
    font-size: 1em;
    text-transform: uppercase;
}
.patient-information__item-body{
    line-height: 1.5em;
    font-size: 1em;
    padding-top: 15px;
}
.patient-information__item-body-title{
    font-weight: 600;
    font-size: 18px;
    margin-top: 5px;
    margin-bottom: 5px;
    text-transform: uppercase;
}
.patient-information__item-body{
    padding-top: 10px;
    padding-bottom: 10px;
    display: none;
}

@media(max-width: 767px){
    .patient-information__item-head{
        font-size: 0.825em;
        padding-bottom: 8px;
    }
    .patient-information__item .patient-information__item-head img{
        margin-right: 5px;
        
    }
    .patient-information__item-body{
        font-size: 0.825em;
    }
    .patient-information__item-body-title{
        font-size: 0.825em;
    }
}