.time-to-end-action__text{
    p{
        display: inline-block;
    }
    & p:first-child{
        border: 2px solid $green;
        text-transform: uppercase;
        font-weight: 600;
        padding-top: 9px;
        padding-bottom: 10px;
        padding-left: 10px;
        padding-right: 10px;
        border-radius: 14px;
        margin-right: 30px;
    }
    & p:last-child{
        border: 2px solid $green;
        font-weight: 600;
        padding-top: 9px;
        padding-bottom: 10px;
        padding-left: 15px;
        padding-right: 15px;
        border-radius: 14px;

        span{
            margin-left: 30px;
            position: relative;
            &::after{
                content: "";
                display: block;
                position: absolute;
                width: 10px;
                height: 10px;
                background-color: $green;
                border-radius: 50%;
                top: 50%;
                margin-top: -5px;
                left: -20px;
            }
        }
        span:first-child{
            margin-left: 0;
            &:after{
                content: unset
            }
        }
    }
}

@media(max-width: 767px){
    .time-to-end-action__text{
        p{
            font-size: .875em;
            margin-bottom: 0;
        }
    }
}

@media(max-width: 460px){
    .services-description__price-tab{
        font-size: 0.825em;
    }
    .time-to-end-action__text{
        
        p{
            &:first-child{
                padding-left: 10px;
                padding-right: 10px;
                padding-top: 6px;
                padding-bottom: 6px;
            }
            &:last-child{
                padding-left: 5px;
                padding-right: 5px;
                padding-top: 6px;
                padding-bottom: 6px;
                margin-top: 10px;
            }
        }
    } 
}