.logo__link{
    display: flex;
    align-items: center;
    text-decoration: none;
}
.logo__company-name{
    font-family: 'Arvo';
    font-weight: 700;
    font-size: 2.5rem;
    color: #66ac4c;
    margin: 0;
    padding: 0;
    text-transform: uppercase;
}
.logo__text{
    padding-left: .5em;
    white-space: nowrap;
}
.logo__company-desck{
    color: #7b7b7b;
    font-weight: 300;
    margin: 0;
    text-transform: uppercase;
    font-size: .825em;
    text-align: right;
}

@media(max-width: 767px){
    .logo__company-name{
        font-size: 1.375em;
    }
    .logo__company-desck{
        font-size: 	0.5em;
        text-align: left;
    }
}