.services-description__content-image{
    width: 45%;
    border-radius: 28px;
    overflow: hidden;

}
.services-description{
    padding-top: 25px;
}
.services-description__tab-content--main-info{
    display: flex;
    justify-content: space-between;
}
.services-description__content{
    // padding-left: 25px;
    // padding-right: 25px;
    margin-left: 25px;
    margin-right: 25px;
}
.services-description__tab-content-text{
  width: 45%;
  margin: 0;
  line-height: 1.5em;
}
.services-description__content-text{
    line-height: 1.5em;
    font-weight: 600; 
}
.services-description{
    background: #fff;
}
.services-description__tab
.services-description__tabs{
    width: 100%;
    max-width: 580px;
    flex-wrap: wrap;
    .services-description__tab{
        width: auto;
        flex-grow: 1;
    }
}
.services-description__ask{
    margin-bottom: 25px;
    svg{
        color: #ef7f1b;
        width: 60px;
        height: 60px;
        margin-right: 25px;
    }
    .btn{
        margin: 0 auto;
    }
}
.services-description__ask-heading{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.services-description__ask{
    position: relative;
}
.services-description__ask-icon-and-text{
    color: #ef7f1b;
    font-size: 26px;
    font-weight: 600;
    max-width: 375px;
   
    display: flex;
    border-right: 2px solid #ef7f1b;
    padding-right: 10px;
}
.services-description__ask-answer{
    max-width: 670px;
    margin: 0;
    line-height: 1.3em;
    padding-left: 20px;
    box-sizing: border-box;
}
.services-description__price-tabs{
    flex-wrap: wrap;
    .tabs-list__item{
        a{
            text-align: left;
        }
    }
}
.services-description__price-tabs-content{
    display: none;
    
}
.services-description__title{
    color: $green;
    padding-left: 25px;
    margin-bottom: 20px;
}
.services-description__price-tabs-content-list{
    list-style: none;
    max-width: 900px;
    li{
        display: flex;
        justify-content: space-between; 
        font-weight: 600;
        margin-bottom: 10px;
        transition: all 0.3s ease-in;
        &:hover{
            color: $green;
        }
    }
}
.services-description__price{
   padding-bottom: 40px;
    h3{
        color: $green;
        font-size: 1.750em;
        font-weight: 600;
        margin-bottom: 20px;
    }
}
.services-description__price-tabs-content.active{
    display: block;
}
.services-description__price-tabs-content-title{
    color: $green;
    font-weight: 600;
    font-size: 1.35em;
}
.services-description__price-tabs .services-description__price-tab{
    width: 50%;
}

@media(max-width: 960px){
    .services-description__tabs{
        max-width: 100%;
    }
    .services-description__ask-heading{
        align-items: center;
        flex-direction: column;
        text-align: center;
    }
    .services-description__content-text,
    .services-description__tab-content-text{
        font-size: 0.825em;
    }
    .services-description__ask-icon-and-text{
        font-size: 20px;
    }
    .services-description__price-tabs-content-list{
        font-size: 0.825em;
    }
    .services-description__ask-answer{
        padding-left: 0;
        margin-bottom: 20px;
    }
    .services-description__ask-icon-and-text{
        border: none;
        padding: 0;
        flex-direction: column;
        svg{
            margin: 0;
        }
    }
}
@media(max-width: 767px){
    .services-description__price-tabs .services-description__price-tab{
        width: 100%;
    }
    .services-description__content-text{
        text-align: center;
    }
    .services-description__tab-content-text{
        text-align: center;
        padding-top: 10px;
        padding-bottom: 10px;
    }
    .services-description__tab-content--main-info{
        flex-direction: column;
    }
    .services-description__tab-content-text{
        width: 100%;
        order: 2;
    }
    .services-description__content-image{
        width: 100%;
    }
}
