.breadcrumbs{
    list-style: none;
    display: flex;
    align-items: baseline;
    flex-wrap: wrap;
    margin-bottom: 25px;
    &__item{
        margin-right: 20px;
        position: relative;
       
        &::before{
            content: "";
            display: block;
            position: absolute;
            width: 6px;
            height: 6px;
            border-bottom: 2px solid #000;
            border-right: 2px solid #000;
            right: -12px;
            transform: rotate(-45deg);
            top: 50%;
            margin-top: -3px;
        }
        a{
            color: #000;
            font-weight: 600;
            text-decoration: none;
            transition: all .1s ease-in;
            font-size: 0.875em;
            &:hover{
                color: $green;
            }
        }
    }
    &__item:last-child{
        &::before{
            content: unset;
        }
    }
}