.registration-doctor-appointment{
    padding-top: 20px;
    padding-bottom: 20px;
    overflow: hidden;
}
.registration-doctor-appointment__form-group{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: center;
    margin-left: -15px;
    margin-right: -15px;

    input{
        border: 1px solid #dbdbdb;
        display: block;
        width: 100%;
        margin-left: 15px;
        margin-right: 15px;
        padding-top: 18px;
        padding-bottom: 20px;
        padding-left: 10px;
        border-radius: 6px;
        margin-bottom: 15px;
    }
    input[name="name"]{
        max-width: 300px;
    }
    input[name="tel"]{
        max-width: 250px;
    }
    input[type="submit"]{
        max-width: 200px;
    }

}
.registration-doctor-appointment__agree{
    font-weight: 600;
    margin: 0;
    margin-bottom: 15px;
    margin-top: 10px;
    a{
        color: #66ac4a;
    }
}

.registration-doctor-appointment__current-city{
    display: flex;
    align-items: center;
    position: relative;
    p{
        font-size: 1.25em;
        color: #2d2928;
        font-weight: 600;
    }
    #registration-doctor__select-office{
        opacity: 0;
        position: absolute;
        left: 80px;
        width: 250px;
        right: 0;
    }
}
.registration-doctor-appointment__city-name{
    margin-left: 10px;
    font-weight: 700;
    color: #f19f3c !important;
    border-bottom: 2px dotted #f19f3c;
}
.registration-doctor-appointment__title{
    color: #5b5c5e;
    font-size: 1.750em;
}
@media(max-width: 767px){
    .registration-doctor-appointment__agree{
        font-size: 0.750em;
    }
   .registration-doctor-appointment__form-group{
        input{
            margin-bottom: 20px;
        }
        input[name="name"]{
            max-width: 100%;
        }
        input[name="tel"]{
            max-width: 100%;
        }
        .primary-btn{
            margin: 0 auto;
            margin-bottom: 0;
        }
    }
}
@media(max-width: 460px){
    .registration-doctor-appointment__current-city{
        font-size: 0.825em;
    }
}