.doctors__filter{
    display: flex;
    margin-left: -15px;
    margin-right: -15px;
    align-items: center;
}
.doctors__filter-item{
    width: 100%;
    max-width: 350px;
    margin-right: 15px;
    margin-left: 15px;
    select{
        width: 100%;
        height: 40px;
        background-color: transparent;
    }
    input{
        width: 100%;
        height: 40px;
        background-color: transparent;
    }
}
.filter-select{
    background-image: url(../img/dropdown-arrow.svg);
    background-size: 15px 15px;
    
    background-repeat: no-repeat;
    padding-left: 10px;
    padding-right: 10px;
    background-position-y: center;
    background-position-x: calc(100% - 5px);
    
}
.filter__search{
    position: relative;
    .search__icon{
        position: absolute;
        top: 0%;
        right: 0;
        width: 40px;
        height: 40px;
        display: flex;
        justify-content: center;
        align-items: center;
        border-left: #d9dbda 2px solid;
        svg{
            color: $green;
            width: 18px;
        }
    }
}
input{
    border: 2px solid #d9dbda;
    outline-color: $green;
    padding-left: 10px;
}
select {
    outline-color: $green;
    border: 2px solid #d9dbda;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
  }
.doctors__list{
    display: flex;
    flex-wrap: wrap;
    list-style: none;
    margin-left: -25px;
    margin-right: -25px;
    margin-top: 35px;
    padding: 0;
    justify-content: center;
}
.doctors__list-item{
    max-width: 182px;
    margin-left: 25px;
    margin-right: 25px;
    margin-bottom: 15px;
    text-align: center;

    img{
        max-width: 100%;
        height: auto;
    }
    a{
        text-decoration: none;
        color: #000;
    }
}
.doctors__list-item-name{
    font-size: 18px;
    margin-top: 10px;
}
.doctors__list-item-experience,
.doctors__list-item-position{
    color: #5b5c60;
    font-size: 14px;
}
@media(max-width: 767px){
    .doctors__filter{
        flex-direction: column;
        margin-left: 0;
        margin-right: 0;
    }
    .doctors__filter-item{
        max-width: 100%;
        margin-bottom: 15px;
    }
    .doctors__list{
        margin-top: 25px;
    }
}

@media(max-width: 460px){
    .doctors__list{
        margin-left: 0;
        margin-right: 0;
    }
    .doctors__list-item{
        max-width: 100%;
        margin: 0;
        width: 100%;
    }
}