.header__top{
    padding-top: 10px;
    padding-bottom: 20px;
   
    .container{
        display: flex;
        align-items: center;
        position: relative;
    }
    border-bottom: 2px solid #c9c8c6;
    .menu-toggle{
        display: none;
    }

}
.header{
    border-bottom: 2px solid #c9c8c6;
}
.header__company-tel-number a{
    color: #ef9f40;;
    font-size:1.125em;
    text-decoration: none;
    font-weight: 600;
    white-space: nowrap;

    svg{
        width: 1.2em;
        height: 1.2em;
        vertical-align: middle;
    }
}
.header__bot{
    padding-top: 22px;
    padding-bottom: 14px;
    background-color: #fff;
    z-index: 200;
    display: flex;
    justify-content: center;
    width: 100%;
}
.header__navigation{
    transition: all 0.3s ease-in;

    .search{
        display: none;
    }
    .header__company-tel-number{
        display: none;
    }
}

@media(max-width: 992px){
    .header__company-tel-number{
        margin: 0 auto;
    }
}

@media(max-width: 767px){
    .addresses-and-schedules{
        display: none;
    }
    .header{
        border: none;
        position: relative;
    }
    .header__top{
        .menu-toggle{
            display: flex;
        }
    }
    .header__bot{
        padding: 0;
        border: none;
        .header__navigation{
            display: none;
            transform: scale(0)
        }
        .header__navigation.show{
            transform: scale(1);
            position: absolute;
            background-color: #f7f7f7;
            left: 0;
            right: 0;
            height: 100vh;
            top: 0;
            bottom: 0;
            z-index: 100;
            display: flex;
            justify-content: flex-start;
            align-items: center;
            padding-top: 115px;
            flex-direction: column;
            .main-nav{
                flex-direction: column;
                .main-nav__item{
                    margin-bottom: 10px;
                }
            }
            .search{
                display: block;
                margin-bottom: 20px;
                max-width: 200px;
            }
            .header__company-tel-number{
                margin-bottom: 20px;
            }
        }
    }
    .header__company-tel-number{
        font-size: 0.875em;
    }
}

@media(max-width: 460px){
    .header__top{
        .container{
            flex-direction: column;
            align-items: flex-start;
        }
        .menu-toggle{
            top: 50%;
            margin-top: -30px;
        }
    }
}