.btn{
    padding-top: 16px;
    padding-bottom: 18px;
    border-radius: 6px;
    display: block;
    max-width: 290px;
    border: none !important;
    font-weight: 600;
    text-align: center;
    text-decoration: none;
    cursor: pointer;
    transition: all .3s ease-in;
    max-height: 50px;
    box-sizing: border-box;
    &:hover{
       opacity: .8;
    }
}
.primary-btn{
    background: #66ac4a;
    color: #fff;
}
.secondary-btn{
    background-color: #ef7f1b;
    color: #fff;
}
.close-btn{
    background: transparent;
    border: none;
    position: absolute;
    top: 10px;
    right: 10px;
    svg{
        width: 1.2em;
        height: 1.2em;
        color: #ef9f40;
    }
}