.about-company{
    box-sizing: border-box;
}
.about-company__text{
    line-height: 1.5em;
}
.about-company__video{
    iframe{
        width: 100%;
        max-width: 560px;
        height: 315px;
    }
}

@media(max-width: 767px){
    .about-company__title{
        font-size: 1.35em;
    }
    .about-company__text{
        font-size: 0.825em;
        line-height: 1.3em;
    }
}