.search{
    position: relative;
    width: 100%;
    max-width: 300px;
    margin-left: auto;
    margin-right: 40px;
    transition: all .3s cubic-bezier(0.165, 0.84, 0.44, 1) ;
    input[type="submit"]{
        opacity: 0;
        position: absolute;
        width: 0;
        right: 0;
    }
    form{
        display: flex;
        width: auto;
    }

    input[type="search"]::-webkit-search-decoration,
    input[type="search"]::-webkit-search-cancel-button,
    input[type="search"]::-webkit-search-results-button,
    input[type="search"]::-webkit-search-results-decoration { display: none; }
    
    input[type="search"]{
        border-radius: 12px;
        border: 2px solid $green;
        padding-top: 4px;
        padding-bottom: 5px;
        outline: none;
        width: 100%;
        padding-left: 15px;
        transition: all .3s ease-in;
        &::placeholder{
            padding-left: 10px;
            font-size: 0.825em;
            text-transform: uppercase;
            color: $green;
            font-weight: 300;
        }
    }
    .icon-search-icon{
        position: absolute;
        top: 7px;
        right: 10px;
        color: $green;
       
        width: 14px;
        height: 14px;
           
    }

}

@media (max-width: 992px){
    .search{
        margin-right: 15px;
        margin-left: 30px;
    }
}
@media(max-width: 767px){
    .search{
        display: none;
    }
}