.main-nav{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: baseline;
    flex-wrap: wrap;
    list-style: none;
    margin-left: -15px;
    margin-right: -15px;
}
.main-nav__item{
    margin-bottom: 10px;
    a{  
        padding-top: 10px;
        padding-bottom: 10px;
        padding-right: 20px;
        padding-left: 20px;
        text-decoration: none;
        color: #2a2a28;
        font-weight: 600;
        transition: all .1s ease-in;
        line-height: 1.250em;
        &:hover{
            color: #66ac4c;
        }
    }
}
.main-nav__item--dropdown{
    position: relative;
}
.main-nav__item--dropdown.open{
    .main-nav__item__dropdown-list{
        display: block;
    }
}
.main-nav__item__dropdown-list{
    position: absolute;
    padding-top: 10px;
    display: none;
    list-style: none;
    z-index: 300;
    min-width: 250px;
    left: calc(50% - 125px);
    li{
        background: $green;
        color: #fff;
        font-size: 1em;
        white-space: nowrap;
        padding-top: 8px;
        padding-bottom: 8px;
        padding-left: 15px;
        padding-right: 15px;
        transition: all 0.1s ease-in;
        &:hover{
            background: rgba(104, 181, 76, 1);
        }
        a{
            color: inherit;
            font-weight: 400;
            &:hover{
                color: #fff;
            }
        }

        
    }
}

@media(max-width: 767px){
    .main-nav__item__dropdown-list{
        position: relative;
        text-align: center;
        width: 100%;    
        
    }
    .main-nav{
        align-items: center;
    }
    .main-nav__item{
        text-align: center;
    }
}