.doctor-apointment-popup{
    position: fixed;
    top: 50%;
    left: 50%;
    width: 300px;
    background: #fff;
    margin-left: -150px;
    margin-top: -200px;
    padding: 20px;
    display: none;
    z-index: 300;
    border-radius: 6px;
    .btn{
        width: 100%;
        display: block;
        padding-top: 10px;
    }
    .form__group{
        margin-bottom: 25px;
    }
}
.doctor-apointment-popup.show{
    display: block;
}
.doctor-apointment-popup__title{
    color: #2a2a28;
    text-align: center;
    font-size: 1em;
    font-weight: 600;
    margin-bottom: 20px;
    margin-top: 10px;
}
.doctor-apointment__choise-time{
    font-size: 0.825em;
    font-weight: 600;
    & label{
        position: relative;
        &::after{
            content: '';
            display: inline-block;
            width: 14px;
            margin-left: 5px;
            margin-right: 10px;
            height: 14px;
            vertical-align: middle;
            border-radius: 50%;
            border: 2px solid #ef9f40;
            box-sizing: border-box;
        }
    }
    & label.on{
       &::after{
        background: $green;
       }
    }
    input{
        opacity: 0;
        position: absolute;
    }
}
.wait-until__select-time{
    border: 1px solid #2a2a28;
    max-width: 140px;
    justify-content: space-between;
    align-items: center;
    display: none;
    &.show{
        display: flex;
    }
    
    button{
        background: transparent;
        border: none;
        display: block;
        position: relative;
        width: 25px;
        height: 25px;
        &::before{
            content: "";
            display: block;
            position: absolute;
            width: 10px;
            height: 10px;
            transform: rotate(45deg);
            left: 50%;
            margin-left: -4px;
            border-top: 2px solid #2a2a28;
            border-left: 2px solid #2a2a28;
            top: 7px;
        }
        
        &:last-child{
            &::before{
                transform: rotate(-135deg);
                top: unset;
                bottom: 7px;
            }
        }
    }
    .wait-until__select-time-minutes{
        button{
            &::before{
                margin-left: 0;
                left: 3px;
            }
        }   
    }
}
.wait-until__select-time-counter{
    font-size: 1.35em;
    font-weight: bold;
}
.wait-until__select-time{
    margin: 0 auto;
    margin-top: 20px;
}
.doctor-apointment__choise-time{
    text-align: center;
}