.scroll-to-top{
    background-color: #ef9f40;
    border-radius: 50%;
    position: fixed;
    right: 78px;
    top: 90%;
    padding: 10px;
    box-sizing: border-box;
    width: 45px;
    height: 45px;
    display: none;
    svg{
        color: #fff;
    }
}

@media(max-width: 767px){
    .scroll-to-top{
        display: none !important;
    }
}