.services__wrapper{
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    margin-right: -15px;
    margin-left: -15px;
}
.services__category-list{
    margin: 0;
    padding: 0;
    list-style: none;
}
.services__category{
    margin-bottom: 30px;
    margin-right: 15px;
    margin-left: 15px;

    a{
        color: $green;
        text-decoration: none;
        transition: all 0.3s ease-in;
        &:hover{
            color: #ef9f40
        }
    }
}
.services__category-list-item{
    margin-bottom: 10px;
    border-bottom: 2px dotted #ef9f40;
    width: fit-content;
    padding-bottom: 2px;
    margin-left: 18px;
    position: relative;
    &::before{
        content: "";
        transition: all 0.3s ease-in;
        display: block;
        position: absolute;
        width: 8px;
        height: 8px;
        border-color: #d9dbda;
        border-right: 2px solid #d9dbda;
        border-bottom: 2px solid #d9dbda;
        transform: rotate(-45deg);
        left: -18px;
        top: 5px;
        opacity: 1;
    }
    &:hover{
        &::before{
            border-color: #ef9f40;
        }
    }
}
.services__category-name{
    font-size: 28px;
    color: #2a2a28;
    font-weight: 600;
    margin-bottom: 15px;
}

@media(max-width: 767px){
    .services__category-name{
        text-align: center;
    }
    .services__category-list{
        margin-bottom: 25px;
        li{
           margin: 0 auto;
           margin-bottom: 10px;
        }
    }
}