.testimonials__wrapper{
    display: flex;
    padding-top: 25px;
    padding-bottom: 70px;
}
.testimonials__item{
    display: flex;
    margin-top: 25px;
    background: #fff;
}
.testimonials__content{
    width: 75%;
}
.testimonials__item-doctor-info{
    color: $green;
    font-weight: 600;
    margin-top: 0;
    span{
        font-weight: 400;
        display: block;
        color: #111c24;
        font-size: 15px;
        margin-top: 10px;
    }
}
.testimonials__item{
    margin-top: 0;
    margin-bottom: 25px;
}
.testimonials__write-testimonial{
    background: #fff;
    padding-top: 20px;
    padding-bottom: 20px;
    padding-left: 20px;
    padding-right: 20px;
    box-sizing: border-box;
    h2{
        font-weight: 600;
        font-size: 18px;
        text-align: center;
        margin-bottom: 15px;
    }
    .form-group{
        margin-top: 15px;
        p{
            font-weight: 600;
            margin: 0;
            margin-bottom: 5px;
        }
    }
    .btn{
        width: 100%;
        margin: 0 auto;
    }
}
.testimonials__item-image{
    min-width: 250px;
    overflow: hidden;
    img{
        max-width: 100%;
        height: auto;
    }
}
.testimonials__item-text-content{
    margin-left: 30px;
    padding-top: 16px;
    padding-bottom: 16px;
}
.testimonials__item-review{
    color: #111c24;
    font-size: 15px;
    line-height: 1.3em;
}

.testimonials__item-author{
    position: relative;
    display: flex;
    align-items: center;
    &::before{
        content: "";
        margin-right: 10px;
        display: block;
        transform: rotate(-135deg);
        width: 8px;
        height: 8px;
        border-bottom: 2px solid $green;
        border-left: 2px solid $green;
    }
}
.testimonials__right-bar{
    margin-left: 25px;
    width: 25%;
    position: relative;
}

@media(max-width: 991px){
    .testimonials__wrapper{
        flex-direction: column;
    }
    .testimonials__content{
        width: 100%;
    }
    .testimonials__right-bar{
        width: 100%;
        margin-left: 0;
    }
    .testimonials__wrapper{
        padding-bottom: 30px;
    }
}
@media(max-width: 767px){
    .testimonials__item{
        flex-direction: column;
        padding-top: 15px;
        padding-left: 15px;
        padding-right: 15px;
    }
    .testimonials__item-image{
        display: flex;
        justify-content: center;
    }
    .testimonials__item-text-content{
        margin-left: 0;
        text-align: center;
    }
    .testimonials__item-author{
        justify-content: center;
    }
    .btn{
        margin: 0 auto;
    }
}