.page-title{
    color: #2a2a28;
    font-weight: 600;
    font-size: 1.5em;
    text-align: center;
    opacity: 1;
    margin-bottom: 25px;
}

@media(max-width: 767px){
    .page-title{
        font-size: 1em;
        margin-bottom: 30px;
    }
}
@media(max-width: 460px){
    .page-title{
        font-size: 0.875em;
    }
}
